<template>
	<div class="face">
		<div class="x-face-detect-modal">
			<video ref="video" autoplay @canplay="onVideoCanPlay" />
			<div v-if="canPlay"></div>
			<div v-else>
				<ASpin size="large" tip="..."></ASpin>
			</div>
			{{ getUserMediaFail ? '' : null }}
			<canvas ref="canvas" :width="width" :height="height" />
			<img :src="imgSrc" />
		</div>
	</div>
</template>
  
<script>
import {
	detectSingleFace,
	nets,
	matchDimensions,
	resizeResults,
	draw,
	SsdMobilenetv1Options,
} from 'face-api.js';
import * as faceapi from 'face-api.js'
export default {
	data() {
		return {
			canvas: null,
			video: null,
			width: 500,
			height: 500,
			canPlay: true,
			getUserMediaFail: false,
			computedService: null,
			options: null,
			imgSrc:''
		};
	},
	created() {
		// 
		this.options = new SsdMobilenetv1Options({
			// 
			// 0.5 
			minConfidence: 0.5,
		});
	},
	async mounted() {
		this.canvas = this.$refs['canvas'];
		this.video = this.$refs['video'];
		this.init();
		this.getUserMedia(
			(streams) => {
				// 
				this.stream = streams;
				// 
				if (this.video) this.video.srcObject = streams;
			},
			(err) => (this.getUserMediaFail = true)
		);
		this.initViewFinder();
		this.detectFace();
	},
	methods: {
		async init() {
			await nets.ssdMobilenetv1.loadFromUri('/models');
		},

		getUserMedia(success, error) {
			const constraints = {
				video: {
					facingMode: 'user',
					width: { ideal: this.width },
					height: { ideal: this.height },
				},
			};
			if (navigator.mediaDevices.getUserMedia) {
				navigator.mediaDevices
					.getUserMedia(constraints)
					.then(success)
					.catch(error);
			} else if (navigator.webkitGetUserMedia) {
				navigator.webkitGetUserMedia(constraints, success, error);
			} else if (navigator.mozGetUserMedia) {
				navigator.mozGetUserMedia(constraints, success, error);
			} else if (navigator.getUserMedia) {
				navigator.getUserMedia(constraints, success, error);
			}
		},
		initViewFinder() {
			// 
			if (!this.video) return;
			const marginLeft = (this.video.videoWidth - this.width) / 2;
			const marginTop = (this.video.videoHeight - this.height) / 2;

			this.viewFinderBox = {
				topLeft: {
					x: marginLeft,
					y: marginTop
				},
				topRight: {
					x: marginLeft + this.width,
					y: marginTop
				},
				bottomLeft: {
					x: marginLeft,
					y: marginTop + this.height
				},
				bottomRight: {
					x: marginLeft + this.width,
					y: marginTop + this.height
				}
			};
		},
		drawViewFinder() {
			// 
			const context = this.canvas?.getContext("2d");
			const rectWidth = 50;

			if (!context) return;

			context.clearRect(0, 0, this.canvas?.width || 0, this.canvas?.height || 0);
			const fontLeft = this.video ? (this.video.videoWidth - 200) / 2 : 200;
			context.font = "20px Arial";
			context.fillText("", fontLeft, 50);

			const keys = Object.keys(this.viewFinderBox);
			keys.forEach((key) => {
				const point = this.viewFinderBox[key];
				if (!point) return;

				context.moveTo(point.x, point.y);

				switch (key) {
					case "topLeft":
						context.lineTo(point.x + rectWidth, point.y);
						context.moveTo(point.x, point.y);
						context.lineTo(point.x, point.y + rectWidth);
						break;
					case "topRight":
						context.lineTo(point.x - rectWidth, point.y);
						context.moveTo(point.x, point.y);
						context.lineTo(point.x, point.y + rectWidth);
						break;
					case "bottomLeft":
						context.lineTo(point.x + rectWidth, point.y);
						context.moveTo(point.x, point.y);
						context.lineTo(point.x, point.y - rectWidth);
						break;
					case "bottomRight":
						context.lineTo(point.x - rectWidth, point.y);
						context.moveTo(point.x, point.y);
						context.lineTo(point.x, point.y - rectWidth);
						break;
					default:
						break;
				}
			});

			context.lineWidth = 2;
			context.strokeStyle = "white";
			context.stroke();
		},
		async detectFace() {
			// 
				// eslint-disable-next-line no-promise-executor-return
			//
			await new Promise(resolve => requestAnimationFrame(resolve));
			//
			this.drawViewFinder();
			if (
				!this.canvas ||
				!this.video ||
				!this.video.currentTime ||
				this.video.paused ||
				this.video.ended
			)
				return this.detectFace();
			// 
			const result = await detectSingleFace(this.video, this.options);
			console.log(result,'result....');
			if (!result) return this.detectFace();
			// 
			const dims = matchDimensions(this.canvas, this.video, true);
			// 
			const resizedResult = resizeResults(result, dims);
			const box = resizedResult.box;
			// 
			// if (!this.checkInViewFinder(box)) return this.detectFace();
			this.drawViewFinder();
			// 
			// draw.drawDetections(this.canvas, resizedResult.box);
			this.drawBox(box, "");
			this.video.pause();
			//
			const image = await this.cameraShoot(
				this.video,
				resizedResult.box.topLeft,
				resizedResult.box.width,
				resizedResult.box.height
			);
			if (!image) {
				this.drawBox(box, "");
				await delay(1000);
				this.video.play();
				return this.detectFace();
			}
			let files = new window.File([image], ".jpeg", {
				type: "image/jpeg"
			});
			console.log(files);
			console.log(image);
			const detectResult= false
			//
			// const detectResult = await this.computedService.faceDetect(
			// 	this.formId,
			// 	files
			// );
			if (!detectResult) {
				this.drawBox(box, "");
				await delay(1000);
				this.video.play();
				return this.detectFace();
			}else{
				this.drawBox(box,'	')
			}
			//
			this.handleStopVideo();
		},
		cameraShoot(video, startPoint, width, height) {
			// 
			const canvas = document.createElement("canvas");
			canvas.width = video.videoWidth;
			canvas.height = video.videoHeight;
			canvas
				.getContext("2d")
				?.drawImage(
					video,
					startPoint.x - 40,
					startPoint.y - 40,
					width + 80,
					height + 80,
					0,
					0,
					canvas.width,
					canvas.height
				)
			return new Promise (resolve =>{
				// eslint-disable-next-line no-promise-executor-return
				 canvas.toBlob(resolve, "image/jpeg")
				this.imgSrc = canvas.toDataURL(resolve, "image/jpeg")
			});
		},
		drawBox(box, label) {
			// box
			if (!this.canvas) return;
			const context = this.canvas.getContext("2d");
			context?.clearRect(box.x, box.y, box.width, box.height);
			const drawBox = new draw.DrawBox(box, {
				label: label
			});
			drawBox.draw(this.canvas);
		},
		handleStopVideo() {
			this.stream?.getTracks().forEach((track) => track.stop());
		},
		onVideoCanPlay() {
			// 
			this.canPlay=true
		},
	},
};
</script>
  
<style lang="scss" scoped>
.x-face-detect-modal {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	position: relative;
	transform: rotateY(0deg);

	canvas {
		position: absolute;
		top: 0;
	}

	video {
		object-fit: fill;
	}
}
</style>
  