<template>
  <div id="map"></div>
</template>

<script>
// import { getMap } from "@/api/https";
import AMapLoader from "@amap/amap-jsapi-loader";
export default {
  data() {
    return {
      key: "fd6697ba33023f67e965a65f8abd69a3",
    };
  },
  async mounted() {
    this.initMap();
  },
  methods: {
    //
    // 
    initMap() {
      AMapLoader.load({
        key: this.key, // WebKey load 
        version: "2.0", //  JSAPI  1.4.15
        plugins: [""], // 'AMap.Scale'
      })
        .then((AMap) => {
          this.map = new AMap.Map("map", {
            pitch:65, //083
            viewMode: "3D", //viewMode3D
            zoom: 17, //
            expandZoomRange: true, // expandZoomRange  true  zoomsPC20
            zooms: [3, 20], //PC[3,18],[3-18];,[3,19],[3-19],19/20
            center: [115.15,36.28], //
          });
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
};
</script>

<style lang="scss" scoped>
#map {
  width: 80%;
  height: 100%;
}
</style>